import React from 'react';
import { roomArray } from '../../room.js'

const Rooms = () => {
    return (
    <section className="rooms">
        <h1>All available rooms</h1>
        {roomArray.map((room)=>{
            return (<div key={room.id} className="card">
                <div className="card__title">{ room.title }</div>
                <div className="card__description">{ room.description }</div>
            </div>)
        })}
    </section>
    )
}

export default Rooms;