import React from 'react';
import { io } from "socket.io-client";
import history from '../../history';
import './chat-box.styles.css'

class ChatBox extends React.Component {
    state = {serverMessage: [], inputText:"", clientCount:0, spinner: true, userTyping:[], isTyping:false}
    socket = null;
    myRef = React.createRef();
    componentDidMount(){
        if(!this.props.userName){history.push("/");return;}
        // to connect with a socket
        this.socket = io("https://chat-app-co.herokuapp.com/", {path:"/global",transports:["polling", "websocket"]});
        // this.socket = io("http://localhost:8001", {path:"/global",transports:["polling", "websocket"]});

        this.socket.on("connect", () => {
            this.setState({spinner:false});
            window.addEventListener("blur", () => {
                this.socket.on("serverMessage", () => {
                    new Audio("https://chat-app-co.herokuapp.com/juntos-607.mp3").play();
                });
                
                
            })
        });

        //listening to custom event Message which display all the message
        this.socket.on("serverMessage", (message) => {
            this.setState({serverMessage: [...this.state.serverMessage, ...message]});
            this.myRef.current.scrollIntoView({behavior: "smooth"})
        });

        //listening for clientCount event
        this.socket.on("userCount", (clientCount) => {
            this.setState({clientCount: clientCount});
        })

        // listening user typing event
        this.socket.on("userStartToType", (userName) => {
            this.setState({userTyping: [...this.state.userTyping, ...userName]});
        })

        this.socket.on("removeUserTyping", (name) => {
            const temp = this.state.userTyping.filter((userName)=> (userName!==name)?userName: false);
            this.setState({userTyping:temp});
        });


    }
    onChatSubmit = (e) => {
        e.preventDefault();
        this.socket.emit("userMessage",this.state.inputText, this.props.userName);
        this.socket.emit("removeUserTyping", this.props.userName);
        new Audio("https://chat-app-co.herokuapp.com/bubble_pop_3.mp3").play();
        this.setState({inputText: "", isTyping:false})
    }
    onTyping = (e)=> {
        this.setState({inputText: e.target.value},()=>{
            if(this.state.inputText === ""){
                this.socket.emit("removeUserTyping", this.props.userName);
                this.setState({isTyping:false})
            }
        });
        // As long as user start typing emit but once
        if(!this.state.isTyping){
            this.socket.emit("userStartToType",this.props.userName);
            this.setState({isTyping: true});
        }
    }
    componentWillUnmount() {
        //emit an event user just unmounted
        if(this.socket)this.socket.emit("userRemove",this.socket.id);

    }
    
    render(){
        if(this.state.spinner){
            return <div className="spinner"></div>
        }
        return(
            <section className="chat">
                <h1 className="chat__heading">Welcome {this.props.userName}</h1>
                <h4 className="chat__secondary">Total users connected {this.state.clientCount}</h4>
                <div className="chat__pane">
                    {this.state.serverMessage.map(
                        messageListItem => {
                            return(
                                <ul key={messageListItem.uuid}>
                                <li className="chat__description" >
                                    <span className={messageListItem.userName===this.props.userName? "usernameBlue": "usernameRed"}>{messageListItem.userName}</span>: <span>{messageListItem.message}</span>
                                </li>
                                <div className="chat__border" ref={this.myRef}/>
                                </ul>
                            )
                        }
                    )}
                    {/* {this.state.userTyping!=="" ? <div className="chat__description">{this.state.userTyping}...</div>: null} */}
                    {this.state.userTyping.map((user,i)=>{
                        return <p key={i}className="chat__typing">{user}
                            <span className="chat__dot1">.</span>
                            <span className="chat__dot2">.</span>
                            <span className="chat__dot3">.</span>
                        </p>
                    })}
                </div>
                <form className="chat__form"onSubmit={(e)=>this.onChatSubmit(e)}>
                    <input className="chat__input" type="text" value={this.state.inputText} onChange={ (e)=>this.onTyping(e) }/>
                    <button className="chat__button" type="submit">Send</button>
                </form>
            </section>
        )
    }
}

export default ChatBox;