import React from 'react';
import './auth.styles.css';

class Auth extends React.Component {
    state = {userName:""}
    render(){
        return (
            <section className="auth">
                <form className="auth__form"onSubmit={(e)=>this.props.onNameSubmit(e,this.state.userName)}>
                    <input className="auth__input" onChange={(e)=> this.setState({userName: e.target.value})}type="text" placeholder="what is your name"/>
                    <button className="auth__button" type="submit">Enter</button>
                </form>
            </section>
        )
    }
}
export default Auth;